            };
            this.noticeList = content;
            this.totalElements = totalElements;
        }
        catch (e) {
            this.$message.error(`获取列表失败！`)
            console.log(e)
        }
        this.loading = false;
    }
    getPage(page?:number) {
        this.pageNum = page || 1;
        this.getNoticeList();
    }
    reset(){
        this.read = null;
        this.keyword = '';
        this.getPage();
    }
    cutDetail(detail:string){
        return detail.slice(0,20)
    }
    handleSelectionChange(val:Array<any>){
        this.checkList = [];
        val.forEach(item =>{
            this.checkList.push(item.id)
        })
    }
    tabSelect(val:any){
        this.read = val;
        this.getPage();
    }
    async setRead(){
        if(this.checkList.length > 0){
            try {
                await api.setRead(this.checkList);
                this.$message.success(`已将选中的公告标记为“已读”！`);
                this.reset();
            }catch (e) {
                this.$message.error(`操作失败！`)
                console.log(e)
            }
        }
        else{
            this.$message.warning(`请至少勾选一条公告！`)
        }
    }
}

